html, body {
  font-size: 18px;
  line-height: 1.5; }

p:last-child {
  margin-bottom: 0; }

h1 {
  letter-spacing: -2px; }

.uk-text-lead {
  color: #949494;
  font-size: 1.3rem; }

#copyright {
  font-family: Montserrat, sans-serif; }
  #copyright p {
    color: #949494 !important; }

.site-footer {
  padding: 20px 0;
  margin: 36px auto 0; }
  .site-footer:before {
    content: "";
    position: absolute;
    top: 0;
    right: 100%;
    left: 0;
    border-top: 1px solid #ddd;
    transition: right 1s ease-out; }
  .site-footer.border-animation:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0 !important;
    left: 0;
    border-top: 1px solid #ddd;
    transition: right 1s ease-out; }

.copyright {
  font-size: 18px;
  margin-bottom: 8px; }

.miller-ad-agency {
  margin: 0 auto; }

.site-footer,
#footer-inner,
#footer-bottom,
#footer-bottom-inner {
